import React from 'react'
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default function Newletters() {
    const settings = {
        infinite: true,
        arrows: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1,
        initialSlide: 0,
        autoplay: true,
        responsive: [
            {
              breakpoint: 1024,
              settings: {
                slidesToShow: 3,
                slidesToScroll: 3,
                infinite: true,
                dots: true
              }
            },
            {
              breakpoint: 600,
              settings: {
                slidesToShow: 2,
                slidesToScroll: 2,
                initialSlide: 2
              }
            },
            {
              breakpoint: 480,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1
              }
            }
        ]
    };
    return (
        <>
            <div className="wrap-why-choose">
                <div className="container">
                    <div className="title-main">
                        Dch v <span>An Pht</span>
                        <p>Xy Dng - C Kh An Pht cung cp cc Dch V Cht lng nht ti Khch hng</p>
                    </div>
                    <Slider {...settings}>
                        <div className="item-choose">
                            <div className="item-choose__img">
                                <i className="fas fa-certificate"></i>
                            </div>
                            <h3>Chuyn Nghip, ng Tin Cy  Nht Khu Vc TPHCM</h3>
                        </div>
                        <div className="item-choose">
                            <div className="item-choose__img">
                                <i className="fas fa-users"></i>
                            </div>
                            <h3>i Ng Nhn Vin C Trnh  Chuyn Mn Cao.</h3>
                        </div>
                        <div className="item-choose">
                            <div className="item-choose__img">
                                <i className="fas fa-tags"></i>
                            </div>
                            <h3>Ti u V Chi Ph V Cht Lng Ca Cc Cng Trnh</h3>
                        </div>
                        
                    </Slider>                    
                    <div className="form-dk">
                        <form className="form-newsletter" method="post" >
                            <div className="newsletter-input">
                                <input type="email" className="form-control" id="ten-newsletter" name="ten-newsletter" placeholder="H tn(*)" required=""/>
                                <div className="invalid-feedback">Vui lng nhp h v tn</div>
                            </div>
                            <div className="newsletter-input">
                                <input type="email" className="form-control" id="email-newsletter" name="email-newsletter" placeholder="Email(*)" required=""/>
                                <div className="invalid-feedback">Vui lng nhp a ch email</div>
                            </div>
                            <div className="newsletter-button">
                                <input type="submit" name="submit-newsletter" value="ng k"/>
                            </div>
                            <div className="contact-now">
                                <p>Lin h ngay</p>
                                <a href="tel:0938477707">0938 477 707</a>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </>
    )
}
